.have-questions {
    display: flex;
    flex-direction: column;
    min-height: 300px; 

    background-color: #f0f0f0;
}

.have-questions__title {
    margin: 55px 0;
    padding: 0;

    font-size: 28px;
    line-height: 30px;  
    font-weight: 600;  
    text-align: center;
    text-transform: uppercase;



}

.have-questions__text {
    margin: 0;
    padding: 0 25px;

    font-size: 19px;
    line-height: 1.45;
    font-weight: 300; 
    text-align: center;   
}

.have-questions__buttons {
    width: 50%;
    margin: 0 auto;    
    display: flex;
    justify-content: space-around;
}

